.content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  max-width: 1500px;

  .link {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 60px;
    height: 100%;
    text-decoration: none;
    color: #2b2929;
    font-weight: bold;

    .countBlock {
      position: relative;

      .count {


      }

      .countProducts {
        position: absolute;
        top: -9px;
        left: 57px;
        width: 25px;
        height: 25px;
        border-radius: 50%;
        font-size: 12px;
        line-height: 13px;
        font-weight: 500;
        border: 1px solid;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }

    &:hover {
      transform: scale(1.3);
      transition: 0.5s ease;
    }

    .logo {
      height: 90px;
      padding: 10px;
    }

  }

  .login {
    //position: relative;
    //text-align: end;
    //width: 100px;

    display: flex;
    justify-content: space-evenly;

    .icon {
      width: 50px;
      height: 50px;
      margin-right: 15px;
    }
  }
}



